/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

h1 {
	font-size: $font-size-h1;
	font-weight: $font-weight-m;
	@include max-mq ($sm) {
		font-size: $font-size-h1 - 5;
	}
	@include max-mq ($mb) {
		font-size: $font-size-h1 - 9;
	}
}
h2 {
	font-size: $font-size-h2;
	font-weight: $font-weight-m;
	@include max-mq ($sm) {
		font-size: $font-size-h2 - 5;
	}
	@include max-mq ($mb) {
		font-size: $font-size-h2 - 10;
	}
}
h3 {
	font-size: $font-size-h3;
	font-weight: $font-weight-sb;
	@include max-mq ($sm) {
		font-size: $font-size-h3 - 1;
	}
}
h4 {
	font-size: $font-size-h4;
	font-weight: $font-weight-sb;
	@include max-mq ($sm) {
		font-size: $font-size-h4 - 1;
	}
}
h5 {
	font-size: $font-size-h5;
	font-weight: $font-weight-sb;
}
h6 {
	font-size: $font-size-h6;
	font-weight: $font-weight-sb;
}