.frm_form_fields  {
	.frm_form_title,
	.frm_description {
	  display: none;
	}
	.frm_fields_container {
	  padding-top: 40px;
	  @include max-mq ($mb) {
		padding-top: 10px;
	  }
	}
	.frm_checkbox {
		position: relative;
		input[type="checkbox"] {
			position: absolute;
			left: 0;
			top: 0;
		}
	}
	.frm_radio {
		margin-bottom: 5px !important;
	}
}
.with_frm_style .vertical_radio .frm_checkbox label, 
.with_frm_style .vertical_radio .frm_radio label {
	padding-left: 0;
	text-indent: 0;
}
.with_frm_style .frm_radio input[type=radio], 
.with_frm_style .frm_checkbox input[type=checkbox] {
	position: absolute;
	left: 0;
	top: 0;
}
.frm_radio,
.frm_checkbox {
	color: $black;
	font-size: $font-size-small;
	letter-spacing: 0;
	line-height: 1.2em;
	padding-left: 25px;
	position: relative;
	label {
	  display: block;
	}
	input {
	  opacity: 0;
	  position: absolute;
	  visibility: hidden;
	  & + span span:first-child {
		align-items: center;
		border: 1px solid $primary-color;
		display: inline-block;
		height: 15px;
		justify-content: center;
		left: 0;
		line-height: 1.3em;
		position: absolute;
		text-align: center;
		top: 0;
		width: 15px;
	  }
	  &:checked + span span:first-child {
		background-color: $secondary-color;
	  }
	}
	a {
		text-decoration: underline;
		@extend %transition-color;
	}
  }