%left-calc {
    padding-left: calc(50vw - ( #{$container-width} / 2 ) - 10px);
    width: 100%;
    @include max-mq ($ls + 10) {
        padding-left: 15px;
    }
}

%right-calc {
    padding-right: calc(50vw - ( #{$container-width} / 2 ) - 5px);
    width: 100%;
    @include max-mq ($ls + 10) {
        padding-right: 15px;
    }
}

%transition-global {
    transition: all .5s;
}

%transition-color {
    transition: color .5s;
}

%after-full {
    background-color: rgba($color: $primary-color, $alpha: 0.2);
    bottom: 0;
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 0;
    @extend %transition-global;
}

%after-gradient {
    content: '';
    position: absolute;
    background-color: $primary-color;
    background: -webkit-linear-gradient(to top, $primary-color 0%, transparent 100%);
    background: -moz-linear-gradient(to top, $primary-color 0%, transparent 100%);
    background: linear-gradient(to top, $primary-color 0%, transparent 100%);
    bottom: 0;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 0;
    @extend %transition-global;
}



